import datePicker from '@/components/CTKY/mx-datepicker/ctkyDatePicker.vue'
import Popup from '@/components/CTKY/uni-popup/uni-popup.vue'
export default {
	components:{
		datePicker,
		Popup
		},
	data() {
		return {
			lineName:'',//线路名称
			date:'选择出发时间',//时间
			// show:false,
		}
	},
	onLoad() {
		var that = this;
		if(that.lineName == '') {
			that.lineName = '请选择线路'
		}
		//监听事件,监听下个页面返回的值
		uni.$on('specialLineName', function(data) {
		    // data即为传过来的值，给上车点赋值
			that.lineName = data.data;
		    //清除监听，不清除会消耗资源
		    uni.$off('specialLineName');
		});
	},
	methods: {
		//------------------------------线路点击------------------------------
		lineTap() {
			console.log('点击了线路');
			uni.navigateTo({
				url:'./specialLinePicker'
			})
		},
		//------------------------------时间点击------------------------------
		timeTap() {
			console.log('点击了时间');
			// this.showPop();
			this.$refs.popup.open()
		},
		//------------------------------下一步点击------------------------------
		nextTap() {
			console.log('点击了下一步');
		},
		// 取消事件
		cancel(){
		    this.close()
		},
		// 确认事件
		sure(e){
		    console.log(e)
		    // 输出 { year: 2020,month: 3,day: 23}
			this.date = e.month + '月' + e.day + '日' + '  ' + e.hour + ':' + e.minute
		},
		// picker显示
		showPop(){
		    this.show = true
		},
		// 关闭picker
		close(){
		    this.show = false
		},
	}
}
